.cardStyle{
	background: rgba(0, 0, 0, 0.6);
	max-width: 300px;
	color:#fff;	
}
.cardStyle:hover{
	-webkit-box-shadow: 0px 0px 15px 10px #000;
	        box-shadow: 0px 0px 15px 10px #000;
    -webkit-transition:  linear 0.5s;
    -o-transition:  linear 0.5s;
    transition:  linear 0.5s;
}
.cardStyle:not(:hover){
	 -webkit-transition:  linear 0.5s;
	 -o-transition:  linear 0.5s;
	 transition:  linear 0.5s;
}


